import wPicker from '@/components/w-picker/w-picker.vue';
export default {
	data() {
		return {
			list: [],
			type: 1,
			ymonth: this.$uitls.yearMonth()
		};
	},
	components: {
		wPicker
	},
	computed: {
		nowYear() {
			let yearMonths = this.$uitls.yearMonth();
			let year = yearMonths.substring(0, 4);
			return year;
		}
	},
	methods: {
		billDetail(item) {
			this.$Router.push({ name: 'bill-detail', params: { data: item } });
		},
		onConfirm(e) {
			this.ymonth = e.checkArr[0] + e.checkArr[1];
			this.getNowMonthOrder();
		},
		changeYearMonth() {
			this.$refs.yearMonth.show();
		},
		getNowMonthOrder() {
			let data = {
				ymonth: this.ymonth
			};
		}
	},
	onLoad() {
		this.type = this.$Route.query.type;
		/*
		 type  1 是车辆账单，2是物业账单
		 */
		this.getNowMonthOrder();
		if (this.type === 1) {
			uni.setNavigationBarTitle({
				title: '车辆账单'
			});
		} else {
			uni.setNavigationBarTitle({
				title: '物业账单'
			});
		}
	}
};
